(function() {
    var script = $('script.abp').get(-1);
    if (!script) {
        return;
    }
    var query = script.src.replace(/^[^\?]+\??/,'').split('&'),
        params = {},
        usesABP;

    for(var i=0; i < query.length; i++) {
        var param = query[i].split('=');
        params[param[0]] = param[1];
    }

    if (params['ch'] == 1) {
        usesABP = true;
    } else if (params['ch'] == 2) {
        usesABP = false;
    }
    window.usesABP = usesABP;
})();
